import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux';
import { getDrugstories } from '../../../api';
import { createNotification } from '../../../functions';
import { Link } from 'react-router-dom';

const DashboardHome = props => {

    if(props.user.is_superuser){
        props.history.push('/dashboard/list');
    }

    const [drugstores, setDrugstores] = useState([]);

    useEffect(() => {
        getDrugstories(props.token)
        .then(res => {
            console.log(res);
            setDrugstores(res)
        })
        .catch(err => {
            console.log(err);
            createNotification('error', 'Nie udało się pobrać drogerii');
        })
    }, [])

    return(
        <div className="border p-5 mx-auto" style={{
            width: '90%',
            maxWidth: '800px',
            marginTop: '200px',
            marginBottom: '500px'
        }}>
            <h1>{props.user.email}</h1>
            <br/>
            Drogerie:<br/><br/>
            {drugstores.map((drugstore, key) => 
                <Link 
                    key={key}
                    to={`dashboard/drugstores/${drugstore.pk}`}
                    className="border m-1 p-2"
                >
                    {drugstore.name}
                </Link>
            )}
        </div>
    )
}


const mapStateToProps = state => ({ 
    user: state.auth.user,
    token: state.auth.token 
});
export const DashboardHomeContainer = connect(mapStateToProps, null)(DashboardHome);